import React from "react"
import './step-inpu.css'


export default ({InputMax, InputValue, BoxId, idProduct, Price}) => {
    function step(op1, op2, BoxId) {
        let value = document.getElementById(op2).getAttribute("value");
        let max = document.getElementById(op2).getAttribute("max");

        if (op1 == 'down') {
            if ( value == 0 ) { return }
            value--;
            if ( value == 0 ) { document.getElementById("product-variable-" + BoxId ).classList.remove("active") }
            document.getElementById(op2).setAttribute("value", value);
            document.getElementById(op2).value = value;
        }
        if (op1 == 'up') {
            if ( max == value ) { return }
            value++;
            if ( value > 0 ) { document.getElementById("product-variable-" + BoxId ).classList.add("active") }
            document.getElementById(op2).setAttribute("value", value);
            document.getElementById(op2).value = value;
        }

        console.log(op1, value , max);
    }
    return (
        <>
            <div className="rs-input-group rs-input-number">
                <span className="rs-input-number-btn-group-vertical">
                    <button onClick={() => step('up', idProduct, BoxId)} type="button" className="rs-btn rs-btn-subtle rs-input-number-touchspin-up"></button>
                    <input
                        autocomplete="off"
                        min={0}
                        max={InputMax}
                        step="1"
                        id={idProduct}
                        price={Price}
                        type="text" className="rs-input" value={InputValue}
                        // onChange={(newValue) => {
                        //     console.log('onChange:', newValue);
                        //     if ( newValue == 0 ) {
                        //         document.getElementById("product-variable-" + BoxId ).classList.remove("active");
                        //     } else {
                        //         document.getElementById("product-variable-" + BoxId ).classList.add("active");
                        //     }
                        // }}
                        />
                    <button onClick={() => step('down', idProduct, BoxId)} type="button" className="rs-btn rs-btn-subtle rs-input-number-touchspin-down"></button>
                </span>
            </div>
        </>
    );
};


